import React from 'react';
import '../../css/GameRooms.css';

const GameRooms = ({ onRoomSelect, onCreateRoom }) => {
    const rooms = ['1', '2', '3', '4'];

    return (
        <div className="rooms-container">
            <h2> </h2>
            <ul>
                {rooms.map((room) => (
                    <li key={room}>
                        {room} <button onClick={() => onRoomSelect(room)}></button>
                    </li>
                ))}
            </ul>
            <button onClick={onCreateRoom}> </button>
        </div>
    );
};

export default GameRooms;
