import React, { useState, useEffect } from 'react';
import '../../css/LoginModal.css';

function LoginModal({ onClose }) {
  const [dragging, setDragging] = useState(false);
  const [pos, setPos] = useState({ x: window.innerWidth / 2, y: window.innerHeight / 2 });

  useEffect(() => {
    const handleMouseMove = (e) => {
      if (dragging) {
        setPos({
          x: Math.min(window.innerWidth - 400, Math.max(0, e.clientX - 400)),
          y: Math.min(window.innerHeight - 110, Math.max(0, e.clientY - 15))
        });
      }
    };

    const handleMouseUp = () => setDragging(false);

    if (dragging) {
      window.addEventListener('mousemove', handleMouseMove);
      window.addEventListener('mouseup', handleMouseUp);
    }

    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('mouseup', handleMouseUp);
    };
  }, [dragging]);

  return (
    <div className="modal-backdrop">
      <div
        className="window"
        style={{ left: `${pos.x}px`, top: `${pos.y}px` }}
        onMouseDown={(e) => {
          setDragging(true);
          e.stopPropagation();
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div className="modal-title-bar">
          <div className="title">  </div>
          <div className="controls">
            <div className="control minimize"></div>
            <div className="control maximize"></div>
            <div className="control close" onClick={onClose}></div>
          </div>
        </div>
        <div className="content-container">
          <p>Microsoft    .</p>
          <form className="login-form">
            <div className="form-group">
              <label htmlFor="username"> (U):</label>
              <input type="text" id="username" value="dyurwitcherry" readOnly />
            </div>
            <div className="form-group">
              <label htmlFor="password">(P):</label>
              <input type="password" id="password" />
            </div>
            <div className="buttons">
              <button type="submit"></button>
              <button type="button" onClick={onClose}></button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default LoginModal;
